  font-style: normal;
}

body {
  font-family: "Raleway";
  font-size: 16px;
  line-height: 1.4;
  font-weight: 400;
  color: #454545;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat";
}

.s-title {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 800;
  font-size: 40px;
  letter-spacing: 0.03em;
  text-align: center;
  color: #454545;
}

.s-title_white {
  color: #fff;
}

.btn-prime {
  position: relative;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 800;
  font-size: 12px;
  color: #FFFFFF;
  background: linear-gradient(180deg, #7D82E2 0%, #686DCC 47.92%, #5256B4 100%);
  box-shadow: 0px 3px 10px rgba(26, 12, 62, 0.25);
  border: none;
  border-radius: 45px;
  color: #fff;
  padding: 20px;
}

.btn-prime:hover {
  background: #5256B4;
}

.btn-prime:active,
.btn-prime:focus {
  outline: none;
}

.btn-prime_reverse {
  color: #5256B4;
  background: #FFF;
}

.btn-prime_reverse:hover {
  background: #FFF;
}

.burger {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  height: 37px;
  width: 50px;
  cursor: pointer;
}

.burger_close {
  justify-content: center;
}

.burger_close .burger__line:nth-child(1) {
  transform: rotate(45deg);
}

.burger_close .burger__line:nth-child(2) {
  display: none;
}

.burger_close .burger__line:nth-child(3) {
  transform: rotate(-45deg);
}

.burger__line {
  height: 2px;
  background: #000;
  transition: 0.3s all;
}

.plus-card {
  margin-left: var(--gutter);
  margin-right: var(--gutter);
  width: calc(100% * 0.25 - var(--gutter) * 2);
  display: flex;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(0deg, #ffffff, #ffffff), linear-gradient(81.34deg, #8ae698 24.32%, #08b688 90.97%);
  box-shadow: 4px 4px 14px rgba(87, 87, 87, 0.25);
  border-radius: 9px;
  padding: 52px 30px 36px;
  transition: 0.3s all;
}

.plus-card:hover {
  box-shadow: 1px 1px 23px rgba(58, 39, 39, 0);
  background: linear-gradient(255.42deg, #8AE698 15.21%, #08B688 115.44%);
}

.plus-card:hover .plus-card__check {
  background: #FFFFFF;
  box-shadow: -4px 4px 10px rgba(2, 183, 150, 0.25);
}

.plus-card:hover .plus-card__check::after {
  background-color: #02B796;
}

.plus-card:hover .plus-card__title,
.plus-card:hover .plus-card__desc {
  color: white;
}

.plus-card__check {
  width: 100px;
  height: 100px;
  border-radius: 100%;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  background: linear-gradient(81.16deg, #8AE698 4.33%, #08B688 99.62%), #FFFFFF;
  box-shadow: -2px 2px 6px rgba(2, 183, 150, 0.25);
  margin: 0 0 50px 0;
  transition: 0.3s all;
}

.plus-card__check::after {
  content: "";
  width: 34px;
  height: 26px;
  mask-image: url("data:image/svg+xml,%3Csvg width='30' height='22' viewBox='0 0 30 22' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M10.7475 21.8558C10.3727 21.8558 10.0132 21.6954 9.74804 21.41L1.26587 12.2705C0.713715 11.6758 0.713715 10.7116 1.26587 10.1174C1.81789 9.52174 2.71302 9.52174 3.26504 10.1174L10.7475 18.1787L26.7124 0.978809C27.2642 0.383662 28.1596 0.383662 28.7114 0.978809C29.2637 1.573 29.2637 2.5372 28.7114 3.13185L11.747 21.41C11.4821 21.6954 11.1224 21.8558 10.7475 21.8558Z' fill='white'/%3E%3C/svg%3E%0A");
  mask-repeat: no-repeat;
  mask-size: contain;
  background-color: #fff;
  transition: 0.3s all;
}

.plus-card__title {
  font-weight: 800;
  font-size: 18px;
  margin-bottom: 17px;
  transition: 0.3s all;
}

.plus-card__desc {
  font-weight: 500;
  font-size: 16px;
  transition: 0.3s all;
}

.equipment-card {
  margin-left: var(--gutter);
  margin-right: var(--gutter);
  width: calc(100% * 0.16667 - var(--gutter) * 2);
}

.equipment-card__thumb-wrap {
  position: relative;
  background: #FFFFFF;
  box-shadow: -2px 2px 6px rgba(2, 183, 150, 0.25);
  overflow: hidden;
  width: 100%;
  padding-top: 100%;
  border-radius: 100%;
  margin-bottom: 20px;
}

.equipment-card__thumb {
  position: absolute;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.equipment-card__desc {
  font-weight: 500;
  font-size: 16px;
  text-align: center;
}

.spec-list {
  margin-left: var(--gutter);
  margin-right: var(--gutter);
  width: calc(100% * 0.83333 - var(--gutter) * 2);
  position: relative;
  padding: 44px 73px;
  margin: 0 auto;
  background: #FFFF;
  box-shadow: -10px 10px 20px rgba(17, 90, 77, 0.16);
  border-radius: 13px;
}

.spec-list__thumb {
  display: block;
  position: absolute;
  bottom: 0;
  right: 0;
}

.spec-list__info {
  max-width: 518px;
}

.spec-list__desc {
  margin-bottom: 48px;
  letter-spacing: 0.03em;
  color: #6D6D6D;
}

.spec-list__list {
  margin-bottom: 48px;
  list-style-type: none;
}

.spec-list__list-item {
  margin-bottom: 11px;
  padding-left: 25px;
  position: relative;
}

.spec-list__list-item::before {
  width: 8px;
  height: 8px;
  border-radius: 100%;
  content: '';
  display: block;
  position: absolute;
  left: 0;
  top: 6px;
  background-image: url("data:image/svg+xml,%3Csvg width='9' height='8' viewBox='0 0 9 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='4.82056' cy='4' r='4' fill='%236469C7'/%3E%3C/svg%3E%0A");
}

.spec-list__dev {
  margin-bottom: 11px;
}

.spec-list__gar {
  margin-top: 40px;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.03em;
  color: #979797;
}

.additional-card {
  margin-left: var(--gutter);
  margin-right: var(--gutter);
  width: calc(100% * 0.25 - var(--gutter) * 2);
}

.additional-card__thumb-wrap {
  width: 130px;
  height: 130px;
  border-radius: 100%;
  display: flex;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
  background: #FFFFFF;
  box-shadow: -4px 4px 15px rgba(7, 101, 84, 0.25);
  margin-bottom: 27px;
}

.additional-card__desc {
  font-weight: 500;
  font-size: 16px;
  color: #fff;
  text-align: center;
}

.product-slide {
  margin-left: var(--gutter);
  margin-right: var(--gutter);
  flex-basis: 0;
  flex-grow: 1;
  max-width: 100%;
  flex-direction: column;
  padding: 36px 25px 20px;
  background: #FFFFFF;
  border: 0.5px solid #D2D2D2;
  box-shadow: 5px 5px 10px rgba(17, 90, 77, 0.16);
  border-radius: 8px;
}

.slick-slider .product-slide {
  display: flex;
}

.product-slide__thumb {
  object-fit: cover;
  width: 100%;
  height: 167px;
  margin-bottom: 46px;
}

.product-slide__title {
  text-align: center;
  font-weight: 500;
  font-size: 12px;
  margin-bottom: 15px;
}

.product-slide__price {
  font-family: "Montserrat";
  text-align: center;
  font-weight: 600;
  font-size: 19px;
  margin-bottom: 19px;
  text-transform: uppercase;
}

.product-slide__order-btn {
  font-size: 14px;
  padding: 14px;
  margin-top: auto;
  border-radius: 4px;
}

.popup {
  display: flex;
  align-items: center;
  padding: 50px;
  justify-content: center;
  position: fixed;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  z-index: -1;
  transform: translateY(-100%);
  visibility: hidden;
  opacity: 0;
  transition: .3s all linear;
}

.popup.page__popup_active {
  z-index: 999;
  overflow: hidden;
  transform: translateY(0);
  visibility: visible;
  opacity: 1;
}

.popup__wrapper {
  width: 403px;
  position: relative;
}

.popup__close {
  width: 35px;
  height: 35px;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 99;
  border-radius: 50%;
  transition: .3s all linear;
  cursor: pointer;
}

.popup__close:hover {
  transform: rotate(90deg);
}

.popup .close__line {
  display: block;
  width: 19px;
  height: 1px;
  background-color: #454545;
  position: absolute;
  top: 50%;
  left: 50%;
}

.popup .close__line:first-child {
  transform: translateX(-50%) translateY(-50%) rotate(45deg);
}

.popup .close__line:last-child {
  transform: translateX(-50%) translateY(-50%) rotate(-45deg);
}

.form {
  background-color: #fff;
  padding: 34px 45px 31px;
