.popup {
    display: flex;
    align-items: center;
    padding: 50px;
    justify-content: center;
    position: fixed;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, .5);
    width: 100%;
    height: 100%;
    z-index: -1;
    transform: translateY(-100%);
    visibility: hidden;
    opacity: 0;
    transition: .3s all linear;

    &.page__popup_active {
        z-index: 999;
        overflow: hidden;
        transform: translateY(0);
        visibility: visible;
        opacity: 1;
    }

    &__wrapper {
        width: 403px;
        position: relative;

    }

    &__close {
        width: 35px;
        height: 35px;
        position: absolute;
        top: 0;
        right: 0;
        z-index: 99;
        border-radius: 50%;
        transition: .3s all linear;
        cursor: pointer;

        &:hover {
            transform: rotate(90deg);
            //background: rgba(152, 255, 174, 0.493);
        }
    }

    .close__line {
        display: block;
        width: 19px;
        height: 1px;
        background-color: $black;
        position: absolute;
        top: 50%;
        left: 50%;

        &:first-child {
            transform: translateX(-50%) translateY(-50%) rotate(45deg);
        }

        &:last-child {
            transform: translateX(-50%) translateY(-50%) rotate(-45deg);
        }
    }

}

.form {
    background-color: #fff;
    padding: 34px 45px 31px;
    color: #505a78;
    display: none;

    &_active {
        display: block;

    }

    @include sm {
        padding: 30px 15px 15px;
    }


    &__title {
        font-size: 30px;
        color: #313644;
        font-weight: 800;
        text-align: center;
        margin-bottom: 8px;

        @include sm {
            font-size: 20px;
        }

    }

    &__subtitle {
        text-align: center;
        color: $black;
        font-size: 14px;
        margin-bottom: 45px;

        @include sm {
            font-size: 14px;
        }


    }

    &__fields,
    img {
        max-width: 100%;
        border: none;
        padding: 0;
    }

    &__field {
        width: 100%;

    }

    &__list {
        list-style: none;
    }

    &__input {
        height: 46px;
        width: 100%;
        line-height: 46px;
        margin-bottom: 16px;
        padding: 0 40px 0 20px;
        background-color: rgba(8, 182, 136, 0.13);
        background-image: url("data:image/svg+xml,%3Csvg width='19' height='15' viewBox='0 0 19 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M6.86792 14.0502C6.63591 14.0502 6.4135 13.9511 6.24928 13.7748L0.999263 8.12712C0.657509 7.75966 0.657509 7.16384 0.999263 6.79669C1.34093 6.42861 1.89498 6.42861 2.23665 6.79669L6.86792 11.778L16.7493 1.14966C17.091 0.781904 17.645 0.781904 17.9867 1.14966C18.3284 1.51683 18.3284 2.11264 17.9867 2.4801L7.48656 13.7748C7.32255 13.9511 7.09995 14.0502 6.86792 14.0502Z' fill='%238DC63F'/%3E%3C/svg%3E");
        background-repeat: no-repeat;
        background-position: center right 20px;
        border: 1px solid #08B688;
        border-radius: 30px;
        outline: 0;

        @include sm {
            height: 40px;
            font-size: 14px;
            margin-bottom: 8px;
        }

        &:focus {
            //border: 1px solid purple;
        }

        &:invalid {
            background: #fff;
        }

    }

    &__input_message {
        min-height: 84px;
        resize: none;

        @include sm {
            min-height: 60px;
            font-size: 14px;
        }


    }

    &__footnote {
        font-size: 12px;
        margin-bottom: 32px;
    }

    &__submit {
        width: 100%;
        font-weight: 900;
        font-size: 16px;
        color: #fff;
        border: none;
        outline: 0;
        cursor: pointer;

        @include sm {
            font-size: 14px;
        padding: 15px;

        }

    }
}

.done {
    display: none;
    background-color: #fff;
    padding: 44px;

    &__pic {
        width: 100%;
        margin-bottom: 14px;
    }

    &__img {
        display: block;
        margin: 0 auto;
    }

    &__title {
        font-size: 30px;
        line-height: 37px;
        font-weight: 800;
        text-align: center;
        color: $black;
        margin-bottom: 2px;
    }

    &__text {
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 22px;
        text-align: center;
        color: $black;
    }
}

.close-popup {
    position  : fixed;
    display   : none;
    width     : 100%;
    height    : 100%;
    background: rgba(0, 0, 0, 0.3);
    top       : 0;
    left      : 0;
    z-index   : 9999;
    @include md{
        visibility: hidden;
        z-index   : -9999;
    }

    &__overlay {
        width   : 100%;
        height  : 100%;
        position: absolute;
        top     : 0;
        left    : 0;
        right   : 0;
        bottom  : 0;
    }

    &__container {
        padding        : 232px 30px 43px;
        width          : 21%;
        position       : absolute;
        top            : 50%;
        left           : 50%;
        transform      : translate(-50%, -50%);
        background     : url(../img/popup/close-popup.png) no-repeat center;
        background-size: cover;

        @include xlg{
            width   : 28%;
            padding : 150px 30px 43px;
        
        }
    }

    &__cls-btn {
        width: 30px;
        height: 30px;
        position: absolute;
        right: 10px;
        top: 10px;
        cursor: pointer;
        transition: 0.3s;
        &:hover {
            transform: rotate(90deg);
        }

    }
    &__cls-btn-line {
        width: 2px;
        height: 100%;
        background: #313644;
        position: absolute;
        top: 50%;
        left: 50%;
        &:nth-child(1) {
            transform: translate(-50%, -50%) rotate(45deg);
        }
        &:nth-child(2) {
            transform: translate(-50%, -50%) rotate(-45deg);
        }
    }

    &__title {
        font-family  : 'Montserrat';
        font-style   : normal;
        font-weight  : 800;
        font-size    : 40px;
        line-height  : 49px;
        text-align   : center;
        margin-bottom: 10px;
        color        : #000;

        @include xlg{
            font-size   : 38px;
            line-height : 48px;
        }
    }

    &__subtitle {
        font-family  : inherit;
        font-style   : normal;
        font-weight  : normal;
        font-size    : 19px;
        line-height  : 26px;
        text-align   : center;
        color        : #000;
        opacity      : 0.8;
        margin-bottom: 30px;
    }

    &__link {
        display       : block;
        width         : 80%;
        padding       : 15px 0;
        background    : #fb4253;
        text-transform: uppercase;
        margin: 0 auto;
        text-decoration: none;
        font-family   : Arial;
        font-style    : normal;
        font-weight   : 400;
        font-size     : 18px;
        text-align    : center;
        color         : #FFFFFF;
        border-radius : 50px;
    }
}

.see-also__href-prod {
    color: rgb(6, 82, 248);
    margin-bottom: 17px;
    text-decoration: none;
    outline: none;
}